import { Injectable } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { BehaviorSubject, Observable, of } from 'rxjs';
import { catchError, tap } from 'rxjs/operators';
import { FunctionsGetResponseBody, ServiceAgreementsHttpService } from '@backbase/data-ang/accesscontrol';

@Injectable()
export class BusinessFunctionsService {
  /**
   * Business function error stream
   */
  readonly businessFunctionsError$ = new BehaviorSubject<HttpErrorResponse | undefined>(undefined);

  constructor(private readonly serviceAgreementsHttpService: ServiceAgreementsHttpService) {}

  /**
   * Return business functions based on provided service agreement id
   * @param id: string - service agreement id
   */
  getBusinessFunctions(id: string): Observable<FunctionsGetResponseBody[]> {
    return this.serviceAgreementsHttpService.getBusinessfunctions({ id }).pipe(
      tap(() => this.businessFunctionsError$.next(undefined)),
      catchError((error: HttpErrorResponse) => {
        this.businessFunctionsError$.next(error);
        return of([]);
      }),
    );
  }
}
