import { Component, Directive, EventEmitter, Input, OnDestroy, Output } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Subject, of, BehaviorSubject, Observable, merge } from 'rxjs';
import { takeUntil, catchError, map, switchMap, filter, mapTo, startWith } from 'rxjs/operators';

import { BbTemplate } from '@backbase/foundation-ang/core';
import {
  BusinessFunction,
  BusinessFunctionGroup,
  FunctionGroup,
  getExtendedBusinessFunctions,
} from '@backbase/accessgroup-common-ang';
import { FunctionsGetResponseBody, FunctionGroupItem } from '@backbase/data-ang/accesscontrol';
import { BusinessFunctionsService } from './services/business-functions.service';
import { JobRoleDetailBusinessFunctionGroupService } from './services/job-role-detail-business-function-group.service';

@Component({
  selector: 'bb-job-role-detail-modal',
  templateUrl: 'job-role-detail-modal.component.html',
})
export class JobRoleDetailModalComponent implements OnDestroy {
  /**
   * Information about Job Role
   */
  @Input() jobRoleDetail!: FunctionGroupItem;

  /**
   * Setter for serviceAgreement Id
   */
  @Input() set serviceAgreementId(id: string) {
    this._serviceAgreementId$.next(id);
  }

  /**
   * Filter hook
   * @default false
   */
  @Input() filterHookEnabled = false;

  private _serviceAgreementId$ = new BehaviorSubject<string | undefined>(undefined);

  /**
   * Close modal window event.
   * Propagate event when modal window should be closed.
   */
  // eslint-disable-next-line @angular-eslint/no-output-native
  @Output() close: EventEmitter<void> = new EventEmitter<void>();

  /**
   * Unsubscribe stream
   */
  private readonly destroy$ = new Subject();

  /**
   * Stream of Grouped business functions
   */
  readonly groupedBusinessFunctions$: Observable<BusinessFunctionGroup[]> = this._serviceAgreementId$.pipe(
    filter((x): x is string => !!x),
    switchMap((id: string) => this.businessFunctionsService.getBusinessFunctions(id)),
    map((allBusinessFunctions: FunctionsGetResponseBody[]) =>
      getExtendedBusinessFunctions(this.jobRoleDetail as FunctionGroup, allBusinessFunctions, this.filterHookEnabled),
    ),
    map((businessFunctions: BusinessFunction[]) => this.groupService.convertToGroups(businessFunctions)),
    takeUntil(this.destroy$),
  );

  /**
   * Stream of loading state for Grouped business functions
   */
  readonly groupedBusinessFunctionsLoading$: Observable<boolean> = merge(
    this.groupedBusinessFunctions$.pipe(
      mapTo(false),
      catchError(() => of(false)),
    ),
  ).pipe(startWith(true));

  /**
   * Stream of error state for Grouped business functions
   */
  readonly groupedBusinessFunctionsError$ = this.businessFunctionsService.businessFunctionsError$;

  constructor(
    private readonly groupService: JobRoleDetailBusinessFunctionGroupService,
    private readonly businessFunctionsService: BusinessFunctionsService,
  ) {}

  /**
   * OpenChange modal window event handler.
   * @param isOpen - boolean
   */
  onOpenChange(isOpen: boolean): void {
    if (!isOpen) {
      this.close.emit();
    }
  }

  ngOnDestroy(): void {
    this.destroy$.next();
  }
}

@Directive({
  selector: '[bbJobRoleDetailModalCustomizable]',
})
export class JobRoleDetailModalCustomizableDirective extends BbTemplate<
  JobRoleDetailModalComponent,
  {
    groupedBusinessFunctions: BusinessFunctionGroup[] | null;
    groupedBusinessFunctionsLoading: boolean | null;
    groupedBusinessFunctionsError: HttpErrorResponse | undefined | null;
  }
> {}
