import { Component, EventEmitter, Input, Output } from '@angular/core';
import { LimitBound } from '@backbase/accessgroup-common-ang';
import { BusinessFunction } from '../model/types';

@Component({
  selector: 'bb-privilege-indicator-list',
  templateUrl: './privilege-indicator-list.component.html',
})
export class PrivilegeIndicatorListComponent {
  /**
   * Business Function to check privileges against
   */
  @Input() businessFunction: BusinessFunction | undefined;

  /**
   * Limit grouped by id and type of permission
   */
  @Input() limitData: any;

  /**
   * Permission base on cxp and user permission
   */
  @Input() userPermission: any;

  /**
   * Function group id
   */
  @Input() functionGroupId: string | undefined;

  /**
   * Business Function to check privileges against
   */
  @Output() emitLimitData = new EventEmitter();

  /**
   * Check whether limit is allowed for specific privilege
   */
  showLimit(privilege: string): boolean {
    // @ts-ignore
    const fgKey = this.functionGroupId + this.businessFunction?.functionId + privilege;
    return (
      this.limitData &&
      (this.limitData[this.businessFunction?.functionId + privilege] || this.limitData[fgKey]) &&
      this.businessFunction?.privileges[privilege].assigned &&
      this.businessFunction?.privileges[privilege].applicable
    );
  }

  /**
   * Get limit context object
   */
  getLimitContext(privilege: string) {
    return {
      limit: this.functionGroupId
        ? this.limitData[this.functionGroupId + this.businessFunction?.functionId + privilege]
        : this.limitData[this.businessFunction?.functionId + privilege],
      id: this.businessFunction?.functionId,
      type: privilege,
    };
  }

  /**
   * Emit limit data
   */
  emitLimit(emitData: any) {
    if (this.functionGroupId) {
      this.emitLimitData.emit({
        ...emitData,
        functionGroupId: this.functionGroupId,
      });
    } else {
      this.emitLimitData.emit(emitData);
    }
  }

  /**
   * Track when control has no amount and no limits button should be shown
   */
  isLimitBoundsEmpty(limitData: { limitBounds: LimitBound[] }): boolean {
    const limitBounds = limitData.limitBounds;
    return limitBounds?.every(bound => !bound.regular?.amount && !bound.shadow?.amount);
  }
}
