import { Component, Directive, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { BehaviorSubject, combineLatest, merge, Observable, of, ReplaySubject } from 'rxjs';
import { auditTime, catchError, map, mapTo, shareReplay, switchMap } from 'rxjs/operators';
import {
  getRouteParams,
  ServiceAgreementWithParticipants,
  UserPermissionsDataGroupDetailsService,
} from '@backbase/accessgroup-common-ang';
import { ServiceAgreementParticipantsGetResponseBody } from '@backbase/data-ang/accesscontrol';
import { ActivatedRoute } from '@angular/router';

const ACCOUNT_GROUP_DETAILS_ERROR = 'ACCOUNT_GROUP_DETAILS_ERROR';

@Component({
  selector: 'bb-account-group-detail-modal',
  templateUrl: './account-group-detail-modal.component.html',
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AccountGroupDetailModalComponent {
  /**
   * Component context
   */
  readonly hostRef: AccountGroupDetailModalComponent = this;

  itemsPerPage = 10;

  /**
   * Flag to show modal
   */
  @Input() isOpen = false;

  @Input() targetSaId = '';

  /**
   * Account group name
   */
  @Input() accountGroupName = '';

  @Input() legalEntityIds = [];

  /**
   * Setter for account Ids
   */
  @Input() set accountIds(val: Array<string> | undefined) {
    if (val) {
      this.selectedAccountGroupSubject.next(val);
      this.selectedAccountGroupAccountsCurrentPageSubject.next(0);
      this.selectedAccountGroupAccountsSearchQuerySubject.next('');
    }
  }

  /**
   * Event emitter that emits on cancel button click
   */
  @Output() cancel = new EventEmitter<void>();

  readonly serviceAgreement$: Observable<ServiceAgreementWithParticipants> = getRouteParams(
    this.activeRoute,
    'serviceAgreementId',
  ).pipe(
    switchMap((id: string | null) => {
      let idSa = id;
      if (this.targetSaId && !id) {
        idSa = this.targetSaId;
      }
      return idSa
        ? this.dataGroupDetailsService.getServiceAgreement(idSa)
        : this.dataGroupDetailsService.serviceAgreementFromContext;
    }),
    shareReplay(1),
  );

  private readonly selectedAccountGroupAccountsCurrentPageSubject = new BehaviorSubject<number>(0);
  readonly selectedAccountGroupAccountsCurrentPage$ =
    this.selectedAccountGroupAccountsCurrentPageSubject.asObservable();

  private readonly selectedAccountGroupAccountsSearchQuerySubject = new BehaviorSubject<string>('');
  readonly selectedAccountGroupAccountsSearchQuery$ =
    this.selectedAccountGroupAccountsSearchQuerySubject.asObservable();

  private readonly selectedAccountGroupSubject = new ReplaySubject<any>();
  readonly selectedAccountGroup$ = this.selectedAccountGroupSubject.asObservable();

  readonly selectedAccountGroupDetails$: Observable<any> = combineLatest([
    this.selectedAccountGroup$,
    this.selectedAccountGroupAccountsCurrentPage$,
    this.selectedAccountGroupAccountsSearchQuery$,
    this.serviceAgreement$,
  ]).pipe(
    auditTime(100),
    switchMap(([items, from, searchQuery, serviceAgreement]) => {
      let legalEntityIds: Array<string> = this.legalEntityIds;
      if (serviceAgreement.participants) {
        legalEntityIds = [];
        serviceAgreement.participants.forEach((el: ServiceAgreementParticipantsGetResponseBody) => {
          if (el.sharingAccounts) {
            legalEntityIds.push(el.id);
          }
        });
      }
      if (items.length) {
        return this.dataGroupDetailsService
          .getAccountGroupItems({
            items: items,
            from: from,
            size: this.itemsPerPage,
            searchTerm: searchQuery,
            legalEntityIds: legalEntityIds,
          })
          .pipe(
            map(accounts => ({
              data: accounts.items,
              totalCount: accounts.totalCount,
            })),
            catchError(() => {
              return of(ACCOUNT_GROUP_DETAILS_ERROR);
            }),
          );
      }
      return of();
    }),
    shareReplay(1),
  );

  readonly selectedAccountGroupError$: Observable<boolean> = this.selectedAccountGroupDetails$.pipe(
    map((data: any) => data === ACCOUNT_GROUP_DETAILS_ERROR),
  );

  readonly loadingSelectedAccount$ = merge(
    this.selectedAccountGroupSubject.pipe(mapTo(true)),
    this.selectedAccountGroupAccountsCurrentPageSubject.pipe(mapTo(true)),
    this.selectedAccountGroupAccountsSearchQuerySubject.pipe(mapTo(true)),
    this.selectedAccountGroupDetails$.pipe(mapTo(false)),
  );

  constructor(
    private dataGroupDetailsService: UserPermissionsDataGroupDetailsService,
    private readonly activeRoute: ActivatedRoute,
  ) {}

  onCancel() {
    this.cancel.emit();
  }

  pageChange(page: number) {
    this.selectedAccountGroupAccountsCurrentPageSubject.next(page);
  }

  searchSubmit(query: string) {
    this.selectedAccountGroupAccountsCurrentPageSubject.next(0);
    this.selectedAccountGroupAccountsSearchQuerySubject.next(query);
  }
}

@Directive({
  selector: '[bbAccountGroupDetailModalCustomizable]',
})
export class AccountGroupDetailModalCustomizableDirective extends BbTemplate<
  AccountGroupDetailModalComponent,
  {
    selectedAccountGroupDetails: null;
    loadingSelectedAccount: boolean | null;
    selectedAccountGroupAccountsCurrentPage: number | null;
    selectedAccountGroupError: boolean | null;
    selectedAccountGroup: null;
  }
> {}
