import { HttpErrorResponse } from '@angular/common/http';
import { WidgetError, parseError as commonParseError } from '@backbase/accessgroup-common-ang';

// Empty interface because package:apps with tsconfig.packaged.json.
// It also fails when using type since it tries to import WidgetError in other files
export type Error = WidgetError;

export const parseError = (error: HttpErrorResponse): Error => commonParseError('AccessgroupUI', error);
