import { Component, Directive, ChangeDetectionStrategy, Input, Output, EventEmitter } from '@angular/core';
import { BbTemplate } from '@backbase/foundation-ang/core';
import { BehaviorSubject, combineLatest, merge, Observable, of, ReplaySubject } from 'rxjs';
import { auditTime, catchError, map, mapTo, shareReplay, switchMap } from 'rxjs/operators';
import {
  getRouteParams,
  ServiceAgreementWithParticipants,
  UserPermissionsDataGroupDetailsService,
} from '@backbase/accessgroup-common-ang';
import { ActivatedRoute } from '@angular/router';
const PAYEE_GROUP_DETAILS_ERROR = 'PAYEE_GROUP_DETAILS_ERROR';
@Component({
  selector: 'bb-payee-group-detail-modal',
  templateUrl: './payee-group-detail-modal.component.html',
  providers: [],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class PayeeGroupDetailModalComponent {
  /**
   * Component context
   */
  readonly hostRef: PayeeGroupDetailModalComponent = this;

  itemsPerPage = 10;

  /**
   * Flag to show modal
   */
  @Input() isOpen = false;

  /**
   * Payee group name
   */
  @Input() payeeGroupName = '';
  @Input() targetSaId = '';

  /**
   * Setter for payee Ids
   */
  @Input() set payeeIds(val: Array<string> | undefined) {
    if (val) {
      this.selectedPayeeGroupSubject.next(val);
      this.selectedPayeeGroupPayeesCurrentPageSubject.next(0);
      this.selectedPayeeGroupPayeesSearchQuerySubject.next('');
    }
  }

  /**
   * Event emitter that emits on cancel button click
   */
  @Output() cancel = new EventEmitter<void>();

  private readonly selectedPayeeGroupPayeesCurrentPageSubject = new BehaviorSubject<number>(0);
  readonly selectedPayeeGroupPayeesCurrentPage$ = this.selectedPayeeGroupPayeesCurrentPageSubject.asObservable();

  private readonly selectedPayeeGroupPayeesSearchQuerySubject = new BehaviorSubject<string>('');
  readonly selectedPayeeGroupPayeesSearchQuery$ = this.selectedPayeeGroupPayeesSearchQuerySubject.asObservable();

  private readonly selectedPayeeGroupSubject = new ReplaySubject<any>();
  readonly selectedPayeeGroup$ = this.selectedPayeeGroupSubject.asObservable();

  readonly serviceAgreement$: Observable<ServiceAgreementWithParticipants> = getRouteParams(
    this.activeRoute,
    'serviceAgreementId',
  ).pipe(
    switchMap((id: string | null) => {
      let idSa = id;
      if (this.targetSaId && !id) {
        idSa = this.targetSaId;
      }
      return idSa
        ? this.dataGroupDetailsService.getServiceAgreement(idSa)
        : this.dataGroupDetailsService.serviceAgreementFromContext;
    }),
    shareReplay(1),
  );

  readonly selectedPayeeGroupDetails$: Observable<any> = combineLatest([
    this.selectedPayeeGroup$,
    this.selectedPayeeGroupPayeesCurrentPage$,
    this.selectedPayeeGroupPayeesSearchQuery$,
    this.serviceAgreement$,
  ]).pipe(
    auditTime(100),
    switchMap(([items, from, query, serviceAgreement]) => {
      if (items.length) {
        return this.dataGroupDetailsService
          .getPayeeGroupContacts(items, { from, query, targetSaId: serviceAgreement.serviceAgreement.id })
          .pipe(
            map(payees => ({
              data: payees.data,
              totalCount: payees.totalCount,
            })),
            catchError(() => {
              return of(PAYEE_GROUP_DETAILS_ERROR);
            }),
          );
      }
      return of();
    }),
    shareReplay(1),
  );

  readonly selectedPayeeGroupError$: Observable<boolean> = this.selectedPayeeGroupDetails$.pipe(
    map((data: any) => data === PAYEE_GROUP_DETAILS_ERROR),
  );

  readonly loadingSelectedPayee$ = merge(
    this.selectedPayeeGroupSubject.pipe(mapTo(true)),
    this.selectedPayeeGroupPayeesCurrentPageSubject.pipe(mapTo(true)),
    this.selectedPayeeGroupPayeesSearchQuerySubject.pipe(mapTo(true)),
    this.selectedPayeeGroupDetails$.pipe(mapTo(false)),
  );

  constructor(
    private dataGroupDetailsService: UserPermissionsDataGroupDetailsService,
    private readonly activeRoute: ActivatedRoute,
  ) {}

  onCancel() {
    this.cancel.emit();
  }

  pageChange(page: number) {
    this.selectedPayeeGroupPayeesCurrentPageSubject.next(page);
  }

  searchSubmit(query: string) {
    this.selectedPayeeGroupPayeesCurrentPageSubject.next(0);
    this.selectedPayeeGroupPayeesSearchQuerySubject.next(query);
  }
}

@Directive({
  selector: '[bbPayeeGroupDetailModalCustomizable]',
})
export class PayeeGroupDetailModalCustomizableDirective extends BbTemplate<
  PayeeGroupDetailModalComponent,
  {
    selectedPayeeGroupDetails: null;
    loadingSelectedPayee: boolean | null;
    selectedPayeeGroupPayeesCurrentPage: number | null;
    selectedPayeeGroupError: boolean | null;
    selectedPayeeGroup: null;
  }
> {}
